import React from "react";
import "./ShopForm.css";
import bag from "./bag.jpg";
import large from "./large.png";


const ShopForm = ({ bagQuantity20, bagQuantity10, bagQuantity, interested, onRouteChange }) => {
  return (
    <div className="centerPersonal mv4 shadow-5 bg-white b--black-10 grow  ">
      <div className="shopCard">
        <h1 className="ma3 pa3 colourPrimary">Shopping Basket</h1>
        <div className="card1 "></div>
        <ul className="list pl0 mt0 measure center">

        {(bagQuantity>0)&&(   //Checks if there is a bag in the cart and adds to the form if needed
          <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
            <img className="w2 h2 w3-ns h3-ns br-100 pa3" src={bag} alt="" />
            <div className="pl3 flex-auto">
              <span className="f6 db black-70">30lt bag</span>
              <span className="f6 db black-70">Living Soil</span>
            </div>
            <div>
              <p className="f6 link  hover-dark-gray pa3">x{bagQuantity}</p>
            </div>
          </li>
        )}

        {(bagQuantity20>0)&&( //Checks if there is a bag in the cart and adds to the form if needed
          <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
            <img className="w2 h2 w3-ns h3-ns br-100 pa3" src={bag} alt="" />
            <div className="pl3 flex-auto">
              <span className="f6 db black-70">20lt bag</span>
              <span className="f6 db black-70">Living Soil</span>
            </div>
            <div>
              <p className="f6 link  hover-dark-gray pa3">x{bagQuantity20}</p>
            </div>
          </li>
          )}  

          {(bagQuantity10>0)&&( //Checks if there is a bag in the cart and adds to the form if needed
          <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
            <img className="w2 h2 w3-ns h3-ns br-100 pa3" src={bag} alt="" />
            <div className="pl3 flex-auto">
              <span className="f6 db black-70">10lt bag</span>
              <span className="f6 db black-70">Living Soil</span>
            </div>
            <div>
              <p className="f6 link  hover-dark-gray pa3">x{bagQuantity10}</p>
            </div>
          </li>
          )}


          {interested && (  //Checks if the customer is interested in a large order  and adds to the form if needed
            <li className="flex items-center lh-copy ph0-l bb b--black-10 padsides">
              <img
                className="w2 h2 w3-ns h3-ns br-100 pa3"
                src={large}
                alt=""
              />
              <div className="pl3 flex-auto">
                <span className="f6 db black-70">500lt +</span>
                <span className="f6 db black-70">Living soil</span>
              </div>
              <div>
                <p className="f6 link  hover-dark-gray pa3">Interested</p>
              </div>
            </li>
          )}


        </ul>
        <div className="card1"></div>
        <div className="m-5">  </div>
        <button
          className="btn text-white colourBlue"
          onClick={() => onRouteChange("checkout")}
          activeClass="active"
          to="basket"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >
          Proceed to Checkout
        </button>
      </div>
    </div>
  );

  // style={{ height: 250, width: 250 }}
};
export default ShopForm;
